


.app{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: auto;     
    height: 97vh;
    min-width: $min_width;
    border-radius: 0.5rem;
    overflow: hidden;
}
.container{
    flex-grow: 1;
    width: 100%;    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: $gray_background;
    overflow: scroll;
}
.lef_container{
    display: flex;
    flex-direction: column;
    min-width: $min_width;
    width: 25%;    
}

.lef_container_middle,.rigth_container_display{
    flex-grow: 1;
    background-color: $gray_background;
    box-shadow: inset 8px 8px 10px #b6b5c9, inset -8px -8px 10px #b6b5c9;
}
.rigth_container{
    display: flex;
    flex-direction: column;
    width: 75%;
    min-width: $min_width;
    flex-grow: 1;
}

.lef_container_Btn_valider_commande, .lef_container_Btn_vider_panier{
    background-color: $primary_color; 
    color: #fff;   
    font-family: $secondary_font_family;
    font-size: 1.2rem;
    text-align: center;
    padding: 0.5rem 1.5rem;
    border-radius: 0.3rem;
    margin-top: 1rem;
    width: 250px;
    cursor: pointer;
    &:hover{
        background-color: #046109;
    }
}
.lef_container_Btn_valider_commande{
    &:hover{
        background-color: #046109;
    }
}

.lef_container_Btn_vider_panier{
    &:hover{
        background-color: red;
    }
}




//------ Media Queries--------
@media screen and (max-width:1250px) {
    //----- Container -------
    .lef_container{
        width: 100%;    
    }
    .rigth_container{
        width: 100%;
    }


    //-------- Navbar -----------
    .navbar_login{
        margin-left: 0.5rem;
        justify-content: space-between;
        width: 100%;
    }
    .navbar_login_btn_admin_image,.navbar_login_connexion{margin: 0.3rem!important;}

    //-------- Display_menu --------
    #ajouter3,#modifier3,#lister3{display: none;}


    //------- Footer ------------
    .footer_input{
        input,select{width: 300px;}
    }
    .rigth_container_footer_top_description textarea{
        margin-top: 0.5rem;
        width: 300px;
    }
    
}






